'use client';

import {
  collection,
  addDoc,
  serverTimestamp,
} from 'firebase/firestore';
import { getFirestoreInstance } from './index';

// Define a type for the contact submission data based on your schema
export interface ContactSubmission {
  name: string;
  email: string;
  message: string;
}

/**
 * Saves a contact form submission to Firestore.
 * @param data The contact form data.
 */
export async function saveContactSubmission(data: ContactSubmission) {
  const firestore = getFirestoreInstance();
  try {
    const submissionsCollection = collection(firestore, 'contact-submissions');
    await addDoc(submissionsCollection, {
      ...data,
      createdAt: serverTimestamp(),
    });
  } catch (error) {
    console.error('Error writing to Firestore: ', error);
    // Re-throw the error to be handled by the calling component
    throw new Error('Failed to save submission. Please try again.');
  }
}
