'use client';

import { initializeApp, getApps, getApp, FirebaseApp } from 'firebase/app';
import { getAuth as getFirebaseAuth, Auth } from 'firebase/auth';
import { getFirestore as getFirebaseFirestore, Firestore } from 'firebase/firestore';
import { firebaseConfig } from './config';

let firebaseApp: FirebaseApp | undefined;
let auth: Auth | undefined;
let firestore: Firestore | undefined;

function initializeFirebase() {
  if (typeof window !== 'undefined') {
    if (!getApps().length) {
      firebaseApp = initializeApp(firebaseConfig);
      auth = getFirebaseAuth(firebaseApp);
      firestore = getFirebaseFirestore(firebaseApp);
    } else {
      firebaseApp = getApp();
      auth = getFirebaseAuth(firebaseApp);
      firestore = getFirebaseFirestore(firebaseApp);
    }
  }
}

initializeFirebase();

export function getFirebaseApp(): FirebaseApp {
  if (!firebaseApp) {
    throw new Error('Firebase has not been initialized.');
  }
  return firebaseApp;
}

export function getAuthInstance(): Auth {
  if (!auth) {
    throw new Error('Firebase Auth has not been initialized.');
  }
  return auth;
}

export function getFirestoreInstance(): Firestore {
  if (!firestore) {
    throw new Error('Firestore has not been initialized.');
  }
  return firestore;
}
